          <option value="11">11 letters</option>
          <option value="12">12 letters</option>
          <option value="13">13 letters</option>
          <option value="14">14 letters</option>
          <option value="15">15 letters</option>
          <option value="collection" :disabled="myCollections.length === 0"
            >My Collection</option
          >
        </select>
        <div class="flex my-10 justify-center flex-wrap ">
          <div
            class="text-container"
            v-for="(letter, index) in wordHidden"
            :key="index"
          >
            {{ letter }}
          </div>
          <button
            @click="addToCollection(this.word)"
            title="Add to collection"
            v-if="!myCollections.includes(this.word) && this.revealed"
          >
            <i class="fas fa-plus-square  text-gray-100"></i>
          </button>
        </div>
        <div class="word-description-container">
          <div class="my-8">
            <p class="text-gray-100 mb-2 font-bold text-sm">Definition:</p>
            <p class="text-white">
              {{ definition }}
            </p>
          </div>
          <div class="my-8">
            <p class="text-gray-100 mb-2 font-bold text-sm ">Synonyms:</p>
            <p v-if="synonyms.length">
              <span
                class="mr-1 my-1 text-white border-gray-300 inline-block border-2 rounded-full px-3 py-1"
                v-for="(synonym, index) in synonyms"
                :key="index"
                >{{ synonym }}
                <button
                  @click="addToCollection(synonym)"
                  title="Add to collection"
                  v-if="!myCollections.includes(synonym)"
                >
                  <i class="fas fa-plus-square text-gray-100 "></i></button
              ></span>
            </p>
            <p v-else class="text-white"> No synonyms found</p>
          </div>
        </div>
        <div class="relative mt-10 mb-2">
          <input
            type="text"
            placeholder="Type spelling here"
            class="px-6 py-2  w-full text-xl  shadow rounded focus:outline-none focus:ring-5 focus:ring-gray-500 "
            :class="{
              'ring-2 ring-green-500': correct,
              'focus:ring-2 focus:ring-red-500': !correct,
            }"
            v-model="spelling"
            @keyup="check"
            :disabled="revealed"
          />
          <div
            class=" text-white absolute right-2 h-full top-0 flex items-center"
            v-if="correct"
          >
            <i class="fas fa-check-circle"></i>
          </div>
        </div>
        <div class="flex justify-between mb-10">
          <button
            class="px-4 py-1 mr-1 rounded w-1/2 bg-purple-800 text-white  font-bold"
            @click="clue"
            v-if="!revealed"
            :disabled="cluesNumber === 0"
          >
             Clue ({{ cluesNumber }})
          </button>
          <button
            class="px-4 py-1 ml-1 rounded w-1/2  text-red-500  font-bold border-2 bg-white"
            @click="surrender"
            v-if="!revealed"
          >
             Surrender
          </button>
          <button
            class="px-4 py-1 rounded w-full bg-purple-800 text-white font-bold"
            @click="loadNewWord"
            v-if="revealed"
          >
            {{ correctMessage }} Click to proceed
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import confetti from "canvas-confetti";
import Header from "./Header";
import Loading from "./Loading";

export default {
  components: {
    Header,
    Loading,
  },
  data() {
    return {
      loading: false,
      word: "",
      definition: "",
      synonyms: [],
      numberOfLetters: "",
      spelling: "",
      wordHidden: "",
      wordArray: [],
      revealed: false,
      cluesNumber: 0,
      correctMessage: "",
      correct: false,
      hasServerError: false,
      myCollections: [],
    };
  },
  methods: {
    speak(word) {
      let msg = word;
      let speech = new SpeechSynthesisUtterance();
      speech.lang = "en-US";
      speech.text = msg;
      speech.volume = 1;
      speech.rate = 1;
      speech.pitch = 1;
      window.speechSynthesis.speak(speech);
    },
    check() {
      if (this.spelling.toLowerCase() === this.word.toLowerCase()) {
        this.wordHidden = this.word.split("");
        this.revealed = true;
        this.correctMessage = " Very Good!";
        this.correct = true;
        confetti({
          particleCount: 500,
          spread: 200,
        });
      }
    },
    checkCluesNumber() {
      if (
        this.word.length === 0 ||
        this.word.length === 1 ||
        this.word.length === 2 ||
        this.word.length === 3
      ) {
        this.cluesNumber = 0;
      } else if (this.word.length === 4) {
        this.cluesNumber = 1;
      } else if (this.word.length === 5 || this.word.length === 6) {
        this.cluesNumber = 2;
      } else if (this.word.length === 7 || this.word.length === 8) {
        this.cluesNumber = 3;
      } else if (this.word.length === 9 || this.word.length === 10) {
        this.cluesNumber = 4;
      } else if (this.word.length === 11 || this.word.length === 12) {
        this.cluesNumber = 5;
      } else {
        this.cluesNumber = Math.round(this.word.length / 2);
      }
    },
    clue() {
      let randomNum = Math.floor(Math.random() * this.wordArray.length);
      while (
        this.wordArray[randomNum] === "" ||
        this.wordArray[randomNum] === " "
      ) {
        randomNum = Math.floor(Math.random() * this.wordArray.length);
      }
      this.wordHidden[randomNum] = this.wordArray[randomNum];
      this.wordArray[randomNum] = "";
      this.cluesNumber--;
    },
    surrender() {
      this.wordHidden = this.word.split("");
      this.revealed = true;
    },
    async fetchRandomWord() {
      let minNumberOfLetters = this.numberOfLetters;
      let maxNumberOfLetters = this.numberOfLetters;

      if (this.numberOfLetters === "") {
        minNumberOfLetters = "3";
        maxNumberOfLetters = "-1";
      }

      const url = `https://api.wordnik.com/v4/words.json/randomWord?useCanonical=true&hasDictionaryDef=true&minCorpusCount=100&maxCorpusCount=-1&minDictionaryCount=1&maxDictionaryCount=-1&minLength=${minNumberOfLetters}&maxLength=${maxNumberOfLetters}&api_key=sjnkcy88fz4lqagxmj7l2jjm197xcgb8j1yb1ev183wm9u2rk`;

      const response = await fetch(url);
      const wordRandom = await response.json();

      return wordRandom;
    },
    async fetchSynonyms(word) {
      const response = await fetch(
        `https://wordsapiv1.p.rapidapi.com/words/${word}/synonyms`,
        {
          method: "GET",
          headers: {
            "x-rapidapi-key":
              "fa03bc319cmsh84c9a1f6576ed63p105974jsnbd1e35e4ba8b",
            "x-rapidapi-host": "wordsapiv1.p.rapidapi.com",
          },
        }
      );
      const wordSynonyms = await response.json();

      return wordSynonyms;
    },
    async fetchDefinition(word) {
      const response = await fetch(
        `https://wordsapiv1.p.rapidapi.com/words/${word}/definitions`,
        {
          method: "GET",
          headers: {
            "x-rapidapi-key":
              "fa03bc319cmsh84c9a1f6576ed63p105974jsnbd1e35e4ba8b",
            "x-rapidapi-host": "wordsapiv1.p.rapidapi.com",
          },
        }
      );
      const wordDefinition = await response.json();

      return wordDefinition;
    },
    async loadNewWord() {
      this.loading = true;
      this.spelling = "";
      this.correctMessage = "";
      this.correct = false;
      this.revealed = false;
      this.wordHidden = "";
      let wordRandom;

      if (this.numberOfLetters === "collection") {
        let myCollections = this.getMyCollections();
        myCollections = JSON.parse(myCollections);
        let randomNum = Math.floor(Math.random() * myCollections.length);
        this.word = myCollections[randomNum];
        this.wordHidden = new Array(myCollections[randomNum].length);
        this.wordArray = myCollections[randomNum].split("");
        this.checkCluesNumber();
      } else {
        try {
          wordRandom = await this.fetchRandomWord();
          this.word = wordRandom.word;
          this.wordHidden = new Array(wordRandom.word.length);
          this.wordArray = wordRandom.word.split("");
          this.checkCluesNumber();
        } catch (err) {
          this.loading = false;
          this.hasServerError = true;
        }
      }
      try {
        const { synonyms } = await this.fetchSynonyms(this.word);
        this.synonyms = synonyms?.slice(0, 10) || [];
        const { definitions } = await this.fetchDefinition(this.word);
        this.definition =
          definitions?.[0]?.definition || " No definition found";
      } catch (err) {
        console.log(err.message);
        this.loading = false;
      }

      this.loading = false;
    },
    getMyCollections() {
      return localStorage.getItem("myCollections");
    },
    addToCollection(word) {
      let myCollections = this.getMyCollections();
      const wordExisting = JSON.parse(myCollections)?.find((value) => {
        return value.toLowerCase() == word.toLowerCase();
      });
      if (wordExisting) {
        this.$toast.error(`"${wordExisting}" is already in your collection`, {
          position: "top-right",
        });
      } else {
        if (myCollections === null) {
          myCollections = [word];
          localStorage.setItem("myCollections", JSON.stringify(myCollections));
        } else {
          myCollections = JSON.parse(myCollections);
          myCollections.push(word);
          localStorage.setItem("myCollections", JSON.stringify(myCollections));
        }
        this.$toast.success(`"${word}" successfully added`, {
          position: "top-right",
        });
      }
      this.loadMyCollections();
    },
    loadMyCollections() {
      this.myCollections = JSON.parse(this.getMyCollections()) || [];
    },
  },

  created() {
    this.loadNewWord();
    this.loadMyCollections();
  },
};
</script>

<style>
.btn {
  @apply w-full px-4 py-2 m-2 bg-green-600 text-white font-bold rounded;
}
.speak-btn {
  font-size: 9rem;
  @apply z-10 text-gray-600 w-60 h-72 md:w-80 lg:w-96 flex items-center justify-center  focus:outline-none hover:shadow-none hover:text-purple-800;
}
.text-container {
  @apply h-8 w-8 md:h-12 md:w-12 mx-1 md:text-3xl border-b-4 border-purple-800 flex justify-center items-center font-extrabold text-white;
}
.word-description-container {
  min-height: 200px;
}
</style>
